import { capitalize } from "../util/helpers";
import isEmpty from 'lodash/isEmpty';
import { DBP_BASE_URL, DBP_KEY } from '../common/util/dbp';

//********************
//	Variables
//********************
var languages = [];
var queriedLanguages = [];
var versions = [];
var versionIds = [];
var filesBaseUrl = "https://fcbhabdm.s3.amazonaws.com/mp3audiobibles2";

FCBH.docReady(() => {
  const searchbar = document.getElementById("Search");
  const languageAutocomplete = document.getElementById("language-search-dropdown");
  const params = new URLSearchParams(window.location.search);
  const languageParam = params.get("language") ? capitalize(params.get("language")) : null
  const versionParam = params.get("version");
  const downloadReCaptchaId = "download-recaptcha";
  const downloadReCaptcha = document.getElementsByClassName("g-recaptcha")[0];
  downloadReCaptcha.id = downloadReCaptchaId;

  addInputsWatchers();

  const onSearchbarValueChange = function (key, value) {
    setLanguageAutocomplete(
      key,
      value,
      languages.map((lang) => lang.language_english)
    );
  };

  const onLanguageChange = function (language) {
    languageAutocomplete.style.display = "none";
    handleLanguageChange(language, false);
    setSearchbar(language);
  };

  function handleLanguageChange(lang, version = "ENGESVN2DA") {
    const selectedLang = languages.find(
      (language) => language.language_english.toLowerCase() === lang?.toLowerCase()
    ) || {
      language_english: "English",
      language_family_code: "ENG",
    };
    fetchVersions(selectedLang, version);
  }

  searchbar.onclick = searchbar.onkeyup = (e) => {
    onSearchbarValueChange(e.which, e.target.value);
  };

  searchbar.addEventListener("keyup", (ev) => handleLanguageAutocompleteInteraction(ev));
  languageAutocomplete.addEventListener("mouseover", (ev) => handleLanguageAutocompleteInteraction(ev));

  searchbar.addEventListener("keydown", function (e) {
    if (e.which == 40 || e.which == 38 || e.which == 13) {
      e.preventDefault(); // stops left and right cursor movement
    }
  });

  function setLanguageAutocomplete(key, value, languageList) {
    if (key !== 38 && key !== 40 && key !== 13) {
      var matches;
      var i;

      var _ret = (function () {
        var div = languageAutocomplete;
        div.innerHTML = "";
        div.style.display = "none";

        var search = value.toLowerCase();
        matches = 0;

        if (search.length > 0) {
          div.style.display = "block";

          var _loop = function _loop() {
            if (matches > 7) {
              return {
                v: {
                  v: void 0,
                },
              };
            }
            var languageName = languageList[i];
            if (languageName.toLowerCase().search(search) == 0) {
              matches++;
              var matchedText = languageName.slice(0, search.length);
              var restOfText = languageName.slice(search.length);
              var d = document.createElement("div");
              d.innerHTML = "<strong>" + matchedText + "</strong>" + restOfText;
              d.onclick = function () {
                onLanguageChange(languageName);
              };
              div.appendChild(d);
            }
          };

          for (i = 0; i < languageList.length; i++) {
            var _ret2 = _loop();

            if (typeof _ret2 === "object") return _ret2.v;
          }

          if (matches == 0) {
            div.style.display = "none";
            console.log("no matches");
          }
        }
      })();

      if (typeof _ret === "object") return _ret.v;
    }
  }

  function handleLanguageAutocompleteInteraction(e) {
    var div = languageAutocomplete;
    var active = div.querySelector(".isActive");

    if (div.style.display === "block") {
      if (e.type === "mouseover" && e.target.nodeName.toLowerCase() != "strong") {
        if (active) {
          active.classList.remove("isActive");
        }
        active = e.target;
        active.classList.add("isActive");
      } else if (e.type == "keyup" && (e.which == 40 || e.which == 38)) {
        e.preventDefault();

        if (active) {
          active.classList.remove("isActive");
        }

        if (e.which == 40) {
          //	down
          active = !active ? div.firstChild : active.nextElementSibling || active;
        } else if (e.which == 38) {
          // up
          if (!active) {
            return;
          }
          active = active.previousElementSibling || active;
        }
        active.classList.add("isActive");
        setSearchbar(active.innerText);
      } else if (e.type == "keyup" && e.which == 13) {
        //	enter was pressed
        if (active) {
          onLanguageChange(active.innerText);
        }
      }
    }
  }

  function setSearchbar(text) {
    searchbar.value = text;
  }

  function setVersion(versionId) {
    const versionSelect = document.querySelector("#mp3-version .w-dropdown-list").childNodes;
    onVersionChange(versionSelect[0].id, versionSelect[0].textContent);
    if (searchbar.value === languageParam) {
      const result = Array.from(versionSelect).filter(element => element.id == versionId)
      onVersionChange(result[0].id, result[0].text)
    }
  }

  //********************
  //	On Document Ready
  //********************
  isLessThenIE10();

  //	Loads all the languages
  //	Also calls function that reads url to display version in url (if any)

  let start = null;

  getLanguages()
    .then( loadDropdownLanguages )
    .catch( console.error )
    .finally(() => {
      document.querySelectorAll(".w-dropdown-list").forEach((el) => {
        el.style["max-height"] = "300px";
      });
  });

  getFilesBaseUrl()
    .then( setFilesBaseUrl )
    .catch( console.error );

  //********************
  //	Functions
  //********************
  function isLessThenIE10() {
    var ua = window.navigator.userAgent;
    var msie = ua.indexOf("MSIE ");

    if (msie > 0) {
      // IE 10 or older => return version number
      var e = parseInt(ua.substring(msie + 5, ua.indexOf(".", msie)), 10);
      if (e < 10) {
        document.getElementById("ie-error").style.display = "block";
      }
    }
  }

  async function getLanguages() {
    const baseUrl = `${DBP_BASE_URL}/api/languages?v=4&key=${DBP_KEY}&limit=150`;

    const { data: { data, meta: { pagination } } } = await FCBH.axiosInstance.get( baseUrl );

    const response = await Promise.all( prepareLanguagesRequest( baseUrl, pagination.total_pages ) )

    return [].concat( getMappedLanguages( data ), flattenLanguages( response ) );
  }

  async function getFilesBaseUrl() {
    const baseUrl = `${DBP_BASE_URL}/audio/location?v=2&key=${DBP_KEY}`;

    const { data } = await FCBH.axiosInstance.get( baseUrl );

    const priorityServer = data.sort((a, b) => { a.priority > b.priority ? 1 : -1 })[0];

    return `${priorityServer.protocol}://${priorityServer.server}${priorityServer.root_path}`;
  }

  function prepareLanguagesRequest( baseUrl, totalPages = 0 ){
    const requests = [];

    for (let page = 2; page < totalPages + 1; page++) {
      requests.push(FCBH.axiosInstance.get( `${baseUrl}&page=${page}` ) )
    }

    return requests;
  }

  function flattenLanguages( response ){
    return response.reduce( ( arr, res ) => arr.concat( getMappedLanguages( res.data.data ) ), []);
  }

  function getMappedLanguages( data ){
    return data.map( x => ( { 
        language_english: x.name,
        language_family_code: x.iso?.toUpperCase(),
        language_name: x.autonym, 
      } )
    ); 
  }

  async function loadDropdownLanguages( data ){
    languages = data;

    handleLanguageChange(languageParam, "ENGESVN2DA");
    setSearchbar(languageParam || "English");
  }

  function setFilesBaseUrl( baseUrl ){
    filesBaseUrl = baseUrl || filesBaseUrl;
  }

  async function fetchVersions(lang, selectedDamId) {
    if ( !lang ) return; 

    versions = [];
    versionIds = [];
    closeSearchDiv();

    const baseUrl = `${DBP_BASE_URL}/library/volume?key=${DBP_KEY}&media=audio&status=live&delivery=digital_download&language_family_code=${lang.language_family_code}&rich=1&v=3`;

    try {
      const { data } = await FCBH.axiosInstance.get( baseUrl );

      if ( isEmpty( data ) ) return;

      for (let i = 0; i < data.length; i++) {
        if (versionIds.indexOf(data[i].dam_id) < 0) {
          versions.push(data[i]);
          versionIds.push(data[i].dam_id);
        }
      }

      renderVersions(versions, selectedDamId);

      if(versionParam) setVersion(versionParam);

      const s = selectedDamId ? selectedDamId : versions[0].dam_id;
      renderCopyright(s);

    } catch (error) {
      console.error(error);
      showErrorWithMessage("An error occurred. Please try again later.");
    }
  }

  function renderVersions(json, selectedDamId) {
    const container = document.querySelector("#mp3-version .w-dropdown-list");
    //	remove previous versions
    while (container.firstChild) {
      container.removeChild(container.firstChild);
    }

    if (json.length > 0) {
      let damId;
      for (let i = 0; i < json.length; i++) {
        const j = json[i];
        const testamentInitial = j.dam_id.slice(6, 7);
        const testament = "";
        if (testamentInitial == "N") {
          testament = "New Testament";
        } else if (testamentInitial == "O") {
          testament = "Old Testament";
        } else if (testamentInitial == "P") {
          testament = "Partial";
        }

        const node = document.createElement("A");
        node.href = "#";
        node.classList.add("dropdown-link", "w-dropdown-link");
        const textNode = document.createTextNode(j.volume_name + " Audio " + j.media_type + " " + testament);
        node.id = j.dam_id;
        node.appendChild(textNode);
        node.addEventListener("click", () => {
          onVersionChange(node.id, node.textContent, true);
          setParams()
        });
        container.appendChild(node);

        if (selectedDamId && selectedDamId.toLowerCase() == j.dam_id.toLowerCase()) {
          node.selected = true;
          damId = j.dam_id;
          onVersionChange(node.id, node.textContent);
        }
      }
    } else {
      const node = document.createElement("A");
      node.href = "#";
      node.classList.add("dropdown-link", "w-dropdown-link");
      var textNode = document.createTextNode("No versions available.");
      node.appendChild(textNode);
      node.id = "noVersion";
      container.appendChild(node);
    }
    if (!selectedDamId) {
      onVersionChange(container.firstElementChild.id, container.firstElementChild.textContent);
      setParams()
    }
  }

  function setParams() {
    const version = document.querySelector("#mp3-version .w-dropdown-toggle div").dataset.value
    const newurl = window.location.protocol + "//" + window.location.host + window.location.pathname + `?language=${searchbar.value}&version=${version}`;
    window.history.pushState({path:newurl},'',newurl);
  }

  function onVersionChange(damId, textContent, isEventChange = false) {
    hideError();
    if (! damId) return;

    const textValue = document.querySelector("#w-dropdown-toggle-0 div");
    textValue.setAttribute("data-value", damId);
    textValue.textContent = textContent;
    textValue.style.overflow = "hidden";

    if ( isEventChange ){
      const textValue = document.querySelector("#w-dropdown-toggle-0 div");
      
      textValue.dispatchEvent(
        new MouseEvent("mouseup", {
          view: window,
          bubbles: true,
          cancelable: false,
        })
      );
      textValue.dispatchEvent(
        new MouseEvent("click", {
          view: window,
          bubbles: true,
          cancelable: false,
        })
      );
    }
   
    renderCopyright(damId);
    return;
  }

  function renderCopyright(damId) {
    if (!damId) {
      return;
    }

    var rootDamId = damId.slice(0, 7); // get room dam id
    var details = {};

    $.get({
      url: `${FCBH.BASE_URL}ci/index.php/dbp/get_audio/` + rootDamId.toUpperCase(),
      dataType: "json",
      success: function (json) {
        if (json) {
          for (var i = 0; i < json.length; i++) {
            if (json[i].dam_id.slice(-2) == "DA") {
              details["publisherMark"] = json[i].mark; // get publisher mark
              for (var j = 0; j < json[i].organization.length; j++) {
                var org = json[i].organization[j];

                if (org.organization_role == "licensor") {
                  details["licensor"] = org.organization;
                  details["licensorURL"] = org.organization_url;
                }

                if (org.organization_role == "holder") {
                  details["holderOrg"] = org.organization;
                  details["holderURL"] = org.organization_url;
                }
              }
            }

            if (json[i].dam_id.slice(-2) == "ET") {
              details["copyrightMark"] = json[i].mark; // get copyright mark
              if (json[i].organization.length > 0) {
                //return cr.organization[0];
                details["copyrighter"] = json[i].organization[0].organization;
                details["copyrighterURL"] = json[i].organization[0].organization_url;
              }
            }
          }
        }

        const copyrightNode = document.querySelector("#wf-form-MP3-Downloads .txt-notice");
        while (copyrightNode.lastChild) {
          copyrightNode.removeChild(copyrightNode.lastChild);
        }

        if (notEmpty(details)) {
          if (details.copyrightMark) {
            const span = document.createElement("span");
            span.innerHTML = details.copyrightMark;
            span.classList.add("block");
            copyrightNode.appendChild(span);
          }
          if (details.copyrighter && details.copyrighterURL) {
            const span = document.createElement("span");
            span.innerHTML = "<a href='" + details.copyrighterURL + "'>" + details.copyrighter + "</a>";
            span.classList.add("block");
            copyrightNode.appendChild(span);
          }
          if (details.licensor && details.licensorURL) {
            const span = document.createElement("span");
            span.innerHTML = "<a href='" + details.licensorURL + "'> " + details.licensor + "</a>";
            span.classList.add("block");
            copyrightNode.appendChild(span);
          }

          if (details.holderOrg && details.holderURL) {
            const span = document.createElement("span");
            span.innerHTML =
              "This Bible audio is brought to you by <a href='" + details.holderURL + "'>" + details.holderOrg + "</a>";
            span.classList.add("block");
            copyrightNode.appendChild(span);
          }
          if (details.publisherMark) {
            const span = document.createElement("span");
            span.innerHTML = details.publisherMark;
            span.classList.add("block");
            copyrightNode.appendChild(span);
          }
        } else {
          copyrightNode.textContent = "No copyright information available.";
        }
      },
      error: function () {
        // error handling
      },
    });
  }

  function onDownloadClicked(event) {
    hideError();

    const name = document.getElementById("mp3-name").value;
    const lastName = document.getElementById("Mp3-Last-Name").value;
    const email = document.getElementById("mp3-email").value;
    const recieveEmails = document.getElementById("Receive Email Updates").checked ? true : false;

    console.log('Downloading', {
      name,
      lastName,
      email,
      recieveEmails,
    });

    if (name == "" || email == "" || lastName == "") {
      showErrorWithMessage("Please fill out all form fields to download this Audio Bible.");
      return;
    }

    if (!lettersOnly(name)) {
      showErrorWithMessage("First Name can only contain letters and spaces.");
      return;
    }

    if (!lettersOnly(lastName)) {
      showErrorWithMessage("Last Name can only contain letters and spaces.");
      return;
    }

    if (!validEmail(email)) {
      showErrorWithMessage("Email address is not valid.");
      return;
    }

    const textValue = document.querySelector("#w-dropdown-toggle-0 div");
    const damId = textValue.getAttribute("data-value");
    if (damId === "noVersion") {
      showErrorWithMessage("Please select an audio bible version.");
      return;
    }

    const i = Array.from(document.querySelectorAll("#mp3-version .w-dropdown-list a")).findIndex(
      (option) => option.id === damId
    );

    if (i < 0) {
      // throw error
      console.log("COULDN'T FIND VERSION");
      showErrorWithMessage("An error occurred. Please try again later.");
      return;
    }

    const v = versions[i];
    if (!v) {
      // throw error
      console.log("VERSION IS NOT TRUE");
      showErrorWithMessage("An error occurred. Please try again later.");
      return;
    }

    const response = grecaptcha.getResponse();
    if(response.length == 0) { 
      //reCaptcha not verified
      return;
    }

    const successContainer = document.querySelector(".w-form-done");
    const linkNode = successContainer.querySelector("a");
    const downloader = document.createElement("a");

    downloader.download = true;
    downloader.href = `${filesBaseUrl}/${v.audio_zip_path}`;
    downloader.textContent = 'here';

    linkNode.parentNode.replaceChild(downloader, linkNode);

    successContainer.style.display = "block";
    downloader.click();

    const obj = {
      first_name: name,
      last_name: lastName,
      timestamp: Date.now().toString(),
      language: v.language_english.toString(),
      language_code: v.language_code.toString(),
      version_dam_id: v.dam_id.toString(),
      version_name: v.volume_name.toString(),
      email: email.toLowerCase(),
      recieve_emails: recieveEmails,
    };
    
    // send to firebase
    FCBH.firebaseDb
      .collection("audioBibleDownloads")
      .add(obj)
      .then(function (docRef) {
        console.log("DOCUMENT WAS SENT");
      })
      .catch(function (error) {
        console.error("Error adding document: ", error);
        console.log("ERROR: DOCUMENT WAS NOT SENT");
      });

    if (!recieveEmails) {
      document.getElementById("wf-form-MP3-Downloads").style.display = 'none';
      event.preventDefault();
    }

    window.scrollTo(0,0);
  }

  function closeSearchDiv() {
    if (document.getElementById("searchDiv")) document.getElementById("searchDiv").style.display = "none";
  }

  function showErrorWithMessage(message) {
    const messageContainer = document.querySelector(".w-form-fail");
    const text = messageContainer.querySelector("div");
    messageContainer.style.display = "block";
    text.innerHTML = message;
  }

  function hideError() {
    const messageContainer = document.querySelector(".w-form-fail");
    messageContainer.style.display = "none";
  }
  //********************
  //	Helpers
  //********************
  function notEmpty(obj) {
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return true;
      }
    }
    return false;
  }

  function lettersOnly(text) {
    const t = text.trim();
    const regex = /^[\p{L} \s.-]*$/u
    return regex.test(t);
  }

  function validEmail(text) {
    var t = text.trim();
    var letters = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return t.match(letters) ? true : false;
  }

  function addInputsWatchers(){
    const inputIds = ["Mp3-Last-Name", "mp3-name"];

    inputIds.forEach(inputId => {
      document.getElementById(inputId)
        .addEventListener("keyup", checkAllowedText )
    });
  }

  function checkAllowedText({ target }){
    if(! lettersOnly(target.value)) target.value = target.value.replace(/[^\p{L} \s.-]+/gu, "");
  }

  document.getElementById("wf-form-MP3-Downloads").addEventListener("submit", onDownloadClicked);
  document.querySelectorAll(`#wf-form-MP3-Downloads input[type='submit']`).forEach((button) => {
    button.addEventListener("click", onDownloadClicked);
  });
});
 
