import last from 'lodash/last';

export function capitalize(sentence) {
    return sentence.charAt(0).toUpperCase() + sentence.slice(1).toLowerCase()
}

export function formatDonationText(amount) {
    const amountWithCommasRegex = /(\d)(?=(\d\d\d)+(?!\d))/g
    const amountParsed = (amount % 1) !== 0 ? amount.toFixed(2).replace(amountWithCommasRegex, "$1,") : amount.toString().replace(amountWithCommasRegex, "$1,")
    return amountParsed
}

export const getFormCampaign = ( objName ) => {
    const campaign = last(window.location.href.split('/'));

    return { 
        campaign,
        localForm: `${objName}/${campaign}`
    };
};
 /**
   * Get's converted amount from server
   * @param {String} fromCurrency
   * @param {String} toCurrency
   * @param {String} fromAmount
   */
  export async function convertCurrency(fromCurrency, toCurrency, fromAmount) {
    const currency_convert_post_url = `${FCBH.BASE_URL}ci/currency_convert/convert`;

    fromAmount = String(fromAmount);
    fromAmount = fromAmount.replace("$", "");
    fromAmount = parseFloat(fromAmount.replace(",", ""));

    const url = currency_convert_post_url + "/" + fromCurrency + "/" + toCurrency + "/" + fromAmount;

    const result = await FCBH.axiosInstance.post(url);

    return result.data;
  }